import React, {useContext} from "react";
import "./WorkExperience.css";
import ExperienceCard from "../../components/experienceCard/ExperienceCard";
import { graphicWork } from "../../portfolio";
import { Fade } from "react-reveal";
import StyleContext from "../../contexts/StyleContext";
import SimpleReactLightbox from "simple-react-lightbox"; 
import { Button, Modal } from 'react-bootstrap';

function MyVerticallyCenteredModal(props) {
    console.log(props);
    return (
      <Modal
        {...props}
        size="xxlg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className="modalWrapper"
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            {props.work.name}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {
              props.work.img.map(element => {
                  return(
                    <img src={element} className="imgcontainer"/>  
                  );      
              })
          }
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }

export default function WorkExperience() {
    const [modalShow, setModalShow] = React.useState(false);
    const [modalData, setModalData] = React.useState({img:[["1"],]});
    return (
        <div id="experience">
            <Fade bottom duration={1000} distance="20px">
            <div className="experience-container" id="workExperience">
                <div>
                    <h1 className="experience-heading">Graphics</h1>
                    <div className="experience-cards-div">
                        {
                            graphicWork.projects.map((work) => {
                                return(
                                    <div className="experienceWrapper" onClick={() => {
                                        setModalShow(true);
                                        setModalData(work);
                                    }} >
                                        <img src={work.image} className="imgcontainer" />
                                    </div>
                                );
                            })
                        }
                    </div>
                </div>
            </div>
            <MyVerticallyCenteredModal
                show={modalShow}
                onHide={() => setModalShow(false)}
                work={modalData}
            />
            </Fade>
        </div>
    );
}
